import { request } from '../../request.js'
import Loading from '../../common/loading/loading.vue'
export default {
	data() {
		return {
			loading: false,
			list:[],
			page:1,
			level:'',
			over:0,
		};
	},
	components: {
		Loading
	},
	onLoad(options) {
		uni.setNavigationBarTitle({
			title: options.name
		})
		this.level = options.id;
		this.getList();
	},
	onReachBottom(){
		getList();
	},
	methods:{
		getList(){
			let that = this;
			if(that.over==1) return '';
			this.loading = false;
			request({
				url: 'index.php?s=/wap/member/getTeamMembersList',
				data:{
					level:this.level,
					page:this.page
				},
				method: 'post'
			}).then(res => {
				that.loading = true;
				if(res.data.list){
					that.list = res.data.list;
					that.page++;
				}else{
					that.over = 1;
				}
			});
		}
	}
}
